import { useState, useEffect, useRef } from "react"
import { Link } from "react-router-dom"
import "../styles/SideBar.scss"
import { IconContext } from "react-icons"
import { FaBars } from "react-icons/fa"
import { connect } from "react-redux";
import useOutsideClick from "../hoc/OutsideClicker"

function SideBar(props) {
	const [menuVisible, openMenuUser] = useState(false)
	const dropdownRef = useRef(null)
	// disable scroll throgth css props
	useEffect(() => {
		if(menuVisible)
			document.body.style.overflow = 'hidden'
		else
			document.body.style.overflow = 'unset'
	}, [menuVisible ])

	useOutsideClick(dropdownRef, () => {
		if(menuVisible) openMenuUser(false)
	})

	return (
		<div className="sidebar">
			<div className="icon_bars" onClick={e => openMenuUser(!menuVisible)}>
				<FaBars />
			</div>
			<div className={`sidebar_content responsive_${menuVisible ? "active": "noactive"}`}>
				<div className="logo_wrapper">
					<Link ref={dropdownRef} onClick={e => openMenuUser(false)} to="/"><span className="logo">FornitureWorker</span></Link>
				</div>
				<ul className="sidebar_ul">
					{
						props.routes.map(route => {
							if(route.isPrivate && (route.role_user >= props.user.role && !!props.user.active && !!props.auth))
								return(
									<li key={route.path}>
										<IconContext.Provider value={{ color: "white", className: "icon_header" }}>
											<Link ref={dropdownRef} onClick={e => openMenuUser(false)} to={route.path}>{route.icon}{route.name}</Link>
										</IconContext.Provider>
									</li>
								)
						})
					}
					{
						props.routes.map(route => {
							if(!route.isPrivate && !props.auth)
								return(
									<li key={route.path}>
										<IconContext.Provider value={{ color: "white", className: "icon_header" }}>
											<Link to={route.path}>{route.icon}{route.name}</Link>
										</IconContext.Provider>
									</li>
								)
						})
					}
				</ul>
			</div>
			
		</div>
	);
}

const mapStateToProps = state => ({
	user: state.user,
	auth: state.app.authorization
})

export default connect(mapStateToProps, null)(SideBar)